@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.2/font/bootstrap-icons.css");
*{
    padding: 0;
    margin: 0;
    transition: 0.7s;
}

.error-p{
    color: rgb(213, 62, 62);
    font-family: monospace;
    font-size: 0.9rem;
    margin-top: 5px;
    text-align: end;
    letter-spacing: 1px;
    font-weight: 800;
}
.Visible-hide{
    visibility: hidden;
}
.Login-main{
    max-width: 370px;
    margin: auto;
    margin-top: 60px;
    text-align: center;
}
body{
    font-family: BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif;
    background-color: rgba(218, 214, 214, 0.73);
    cursor: default;
    user-select: none;
}
.box{
    border: 1px solid rgba(219,219,219);
    border-radius: 10px;
    box-shadow: 1px 3px 20px 0 black;
    padding: 30px 25px;
    background-color: white;
}
.header-Log{
    margin-bottom: 35px;
    display: block;
}
.header-Log .logo_name{
    letter-spacing: 2px;
    color: #300171b6;
    font-size: 16px;
    font-weight: bold;
}
.header-Log h5{
    font-size: 22px;
    color: #314e8b;
    margin-top: 10px;
}
form{
    text-align: left;
}
.header-Log img{
    width: 62px;
    border-radius: 50%;
    border: 2px solid #385185;
}
a{
    text-decoration: none;
}
a:active{
    color: red;
}
li{
    padding: 10px;
}
.password{
    display: flex;
    margin-top: 2px;
    border-radius: 4px;
    background:  rgba(250,250,250);
}

.loader {
    border: 5px solid #e0c9c9;
    border-top: 5px solid rgb(105, 18, 129);
    border-radius: 50%;
    width: 35px;
    height: 35px;
    animation: spin 1s linear infinite;
    margin: auto;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
.input , .password{
    border: 2px solid rgba(148, 142, 142, 0.485);
    background:  rgb(236 216 238) !important;
}
.password:hover #show_hide_password{
    display: block;
}
.password .inputx{
    background:  rgb(236 216 238);
    border: none;
    outline: none;
    font-size: 14px;
    padding: 10px;
    width: 100%;
}
#show_hide_password{
    width: 20%;
    text-align: center;
    font-size: 21px;
    color: #500451;
    display: none;
    padding-top: 9px;
    cursor: pointer;
    outline: none;
}
.input , .inputx{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px !important;
    font-weight: 500;
    letter-spacing: 2px;
    color: #630064;
}
.input{
    width: 92%;
    outline: none;
    padding: 9px 0 7px 8px;
    font-size: 14px;
    padding: 10px;
    margin-top: 2px;
    background:  rgba(250,250,250);
    border-radius: 4px;
}

.disable{
    cursor: not-allowed;
    background-color: rgb(80, 135, 171);
}
.input_box{
    margin: 20px 0;
}
.label{
    font-weight: 700;
    text-transform: capitalize;
    color: rgb(71, 75, 79);
    margin-left: 3px;
}
.Log_Button{
    padding: 8px;
    width: 100%;
    background-color: var(--Back);
    outline: none;
    color: white;
    font-size: 15px;
    border: 1px solid transparent;
    border-radius: 5px;
    margin-top: 10px;
    font-weight: bold;
    opacity: 1;
    transition: 0.5s;
    cursor: pointer;
}
.Log_Button:hover{
    background-color: var(--Back);
}
.Log_Button:disabled{
    background-color: rgb(74, 84, 136);
}
.Log_Button:active{
    background-color: #39637e;
    transform: translateY(8px);
}
.or{
    padding-top: 20px;
    font-size: 12px;
    font-weight: 400;
    padding-bottom: 20px;
    display: flex;
}
.osr{
    margin: 0 18px;
}
.desd{
    background-color: rgba(149,149,149);
    height: 1px;
    position: relative;
    width: 100%;
    top: 0.45em;
}
.crea{
    font-size: 15px;
}
.forget{
    color: #560374b6;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
}
.bi-arrow-right{
    font-size: 18px;
    font-weight: bold;
}
.sign_up{
    font-size: 18px;
    color: #560374b6;
    text-decoration: none;
    font-weight: bold;
}
.box2{
    font-size: 16px;
    padding: 18px;
}

.Footer{
    margin-top: 55px;
    margin-bottom: 20px;
    text-align: center;
}
.Footer span{
    font-size: 15px;
    font-weight: bold;
    color: #8e8e8e;
}
.Footer span a{
    color: #8e8e8e;
    letter-spacing: 2px;
    text-decoration: underline;
}
::-webkit-scrollbar{
    width: 5px;
}